<template>
  <section id="services" class="block bg-dark-blue bg-fixed bg-blend-lighten bg-service-texture bg-cover py-20 lg:py-32">
    <div class="w-full h-full">
      <div class="container mx-auto">
        <div class="block text-center">
          <h2 class="text-white font-bold text-3xl pb-4 inline-block relative texture-white">
            Services
          </h2>
        </div>
        <p class="text-white font-light text-md lg:text-xl my-2 lg:my-10 block text-center">Typically, I Provide Web, Android, iOS App Development and UI/UX Design Services.</p>
        <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-10 md:gap-10 lg:gap-6 xl:gap-14 my-6 mx-0 md:mx-0 lg:mx-0 xl:mx-10 content-center">
          <div v-for="service in services" :key="service.title" class="text-center service">
            <div class="shadow-md w-full block md:inline-block service bg-white rounded-md transform hover:scale-105 hover:shadow-dark transition ease-out-expo duration-700">
              <div class="relative block h-60 z-40">
                <div :class="`absolute bg-gradient-to-b ${service.gradient} w-full rounded-md h-60 flex flex-wrap content-center`">
                  <div class="w-full">
                    <img :src="`/images/services/${service.image}`" :alt="service.image" class="h-36 mx-auto cursor-pointer">
                  </div>
                  <h2 class="w-full justify-center mt-2 filter drop-shadow text-center text-white font-md text-xl">{{service.title}}</h2>
                </div>
              </div>
              <div class="bg-opacity-95 rounded-b-md service-content py-3 overflow-hidden bg-white" style="margin-top: -100px">
                <div class="flex rounded-md flex-wrap justify-between">
                  <div v-for="tool in service.tools" class="flex w-3/6 pl-6 my-2" :key="tool">
                    <div class="flex flex-wrap mr-1 content-center">
                      <img :src="`/images/services/panel/${tool.image}`" :alt="tool.image" class="h-4 object-contain w-4">
                    </div>
                    <p class="text-sm text-light-gray text-left"> {{tool.name}}</p>
                  </div>
                </div>
              </div>

            </div>
          </div>

        </div>
        <p class="text-white  font-light text-2xl lg:text-3xl mt-20 lg:mt-20 block w-full md:flex justify-center text-center items-center">Let's build some stuffs! <a href="https://www.fiverr.com/hanifsheikh95" target="_blank" class="block text-center md:inline-block ml-0 md:ml-5 mt-5 md:mt-0">
            <button class="bg-indigo-500 hover:bg-indigo-400 border-b-4 border-indigo-700 hover:border-indigo-500 transition duration-700 ease-out-expo text-white font-bold py-2 px-3 md:px-4 rounded inline-flex items-center">
              <img src="@/assets/images/shopping-cart.svg" alt="shopping-cart" class="inline-block w-6 mr-2">
              <span class="text-sm md:text-md"> Order Now</span>
            </button>
          </a>
        </p>

      </div>
    </div>

  </section>
</template>

<script>
import $ from "jquery";
import anime from "animejs";
function rotate30(element) {
  anime({
    targets: element,
    rotate: 30,
    duration: 800,
    easing: "easeOutElastic(1, .4)",
    delay: 0,
  });
}
function rotate0(element) {
  anime({
    targets: element,
    rotate: 0,
    duration: 800,
    easing: "easeOutElastic(1, .4)",
    delay: 0,
  });
}
export default {
  name: "Service",
  mounted() {
    $(".service img").on("click", function (e) {
      e.stopImmediatePropagation();
      let serviceContent = $(this).closest(".service").find(".service-content");
      serviceContent.toggleClass("active");
      if (serviceContent.hasClass("active")) {
        serviceContent.animate({
          "margin-top": "0px",
        });
        rotate30(this);
      } else {
        serviceContent.animate({
          "margin-top": "-100px",
        });
        rotate0(this);
      }
    });
  },

  data() {
    return {
      services: [
        {
          title: "Web Design",
          gradient: "from-yellow to-deep-yellow",
          image: "web-design.png",
          tools: [
            {
              name: "HTML",
              image: "html.svg",
            },
            {
              name: "Tailwind",
              image: "tailwind.svg",
            },
            {
              name: "JavaScript",
              image: "javascript.svg",
            },
            {
              name: "Bootstrap",
              image: "bootstrap.svg",
            },
          ],
        },
        {
          title: "Web Development",
          gradient: "from-magenta to-brick-magenta",
          image: "web-development.svg",
          tools: [
            {
              name: "React",
              image: "react.svg",
            },
            {
              name: "Laravel",
              image: "laravel.svg",
            },
            {
              name: "Vue",
              image: "vue.svg",
            },
            {
              name: "NodeJS",
              image: "nodejs.svg",
            },
          ],
        },
        {
          title: "UI/UX Design",
          gradient: "from-teal to-deep-teal",
          image: "app-development.svg",
          tools: [
            {
              name: "Figma",
              image: "figma.svg",
            },
            {
              name: "Adobe XD",
              image: "adobe-xd.svg",
            },
            {
              name: "Android Studio",
              image: "android-studio.svg",
            },
            {
              name: "Flutter",
              image: "flutter.svg",
            },
          ],
        },
      ],
    };
  },
};
</script>
 