<template>
  <section id="contact" class="block bg-dark-blue h-full bg-cover pt-20 lg:pt-20 bg-fixed ">
    <div class="w-full h-full">
      <div class="container mx-auto contact">
        <div class="block text-center mb-10 lg:mb-20">
          <h2 class="text-white font-bold text-3xl pb-4 inline-block relative texture-white">
            Contact
          </h2>
        </div>
        <div class="mx-2 flex flex-col lg:flex-row items-center justify-center mb-10 lg:mb-20 space-y-5 lg:space-y-0 lg:space-x-20">
          <p class="text-white text-md md:text-lg font-light inline-block md:flex text-left md:text-center w-full md:w-auto items-center">
            <a target="_blank" href="https://t.me/imhanifsheikh">
              <img src="@/assets/images/telegram.svg" alt="telegram" class="contact-icon h-8 md:h-9 mx-2 transform scale-100 inline-block -mt-1 hover:scale-110 duration-300 transition ease-out" />
              +8801725467151
            </a>
          </p>
          <p class="text-white text-md md:text-lg font-light inline-block md:flex text-left md:text-center  w-full md:w-auto items-center">
            <a target="_blank" href="https://api.whatsapp.com/send?phone=8801725467151">
              <img src="@/assets/images/whatsapp.svg" alt="whatsapp" class="contact-icon h-8 md:h-9 mx-2 transform scale-100 inline-block -mt-1 hover:scale-110 duration-300 transition ease-out" />
              +8801725467151
            </a>
          </p>
          <p class="text-white text-md md:text-lg font-light inline-block md:flex text-left md:text-center  inline-block w-full md:w-auto items-center">
            <a target="_blank" href="https://join.skype.com/invite/baZSft06UDSw">
              <img src="@/assets/images/skype.svg" alt="skype" class="contact-icon h-8 md:h-9 mx-2 transform scale-100 inline-block -mt-1 hover:scale-110 duration-300 transition ease-out" />
              imhanifsheikh@hotmail.com
            </a>
          </p>
        </div>
        <div class="flex flex-col-reverse lg:flex-row items-center">
          <div class="block lg:flex flex-col flex-wrap  mb-6 w-full lg:w-5/6 xl:1/2">
            <p class="text-lg text-center text-white mt-4 lg:mt-0" v-if="successMessage">
              <img src="@/assets/images/message.svg" alt="message" class="inline-block w-14 m-2" />
              {{ successMessage }}
            </p>
            <div class="block container mx-auto" v-else>
              <div class="block text-left mb-0 lg:mb-2">
                <h2 class="text-white font-light text-2xl lg:text-3xl pb-4 inline-block relative ">
                  Get In Touch
                </h2>
              </div>
              <div class="w-full block lg:flex">
                <div class="w-full block pr-0 lg:pr-3 mb-6">
                  <label class="block text-gray-400 text-sm font-bold mb-2" for="name">
                    Name <span class="text-red">*</span>
                  </label>
                  <input @input="checkRequired('name', 'text')" v-model="name" spellcheck="false" class="shadow block appearance-none rounded w-full py-2 px-4 text-gray-400 leading-tight focus:outline-none focus:bg-light bg-dark-gray focus:border-teal border-2 transition duration-300 ease-out border-charcoal-gray" id="name" type="text" />
                  <p v-if="name_error" class="text-red my-2 text-xs italic">
                    Please fill out this field.
                  </p>
                </div>
                <div class="w-full block mb-6">
                  <label class="block text-gray-400 text-sm font-bold mb-2" for="name">
                    Subject <span class="text-red">*</span>
                  </label>
                  <input @input="checkRequired('subject', 'mixed')" v-model="subject" spellcheck="false" class="shadow block appearance-none rounded w-full py-2 px-4 text-gray-400 leading-tight focus:outline-none focus:bg-light bg-dark-gray focus:border-teal border-2 transition duration-300 ease-out border-charcoal-gray" id="subject" type="text" />
                  <p v-if="subject_error" class="text-red my-2 text-xs italic">
                    Please fill out this field.
                  </p>
                </div>
              </div>
              <div class="w-full w-full mb-6">
                <label class="block text-gray-400 text-sm font-bold mb-2" for="email">
                  Email Address <span class="text-red">*</span>
                </label>
                <input @input="checkRequired('email', 'email')" v-model="email" class="shadow block appearance-none rounded w-full py-2 px-4 text-gray-400 leading-tight focus:outline-none focus:bg-light bg-dark-gray focus:border-teal border-2 transition duration-300 ease-out border-charcoal-gray" id="email" type="email" />
                <p v-if="email_error" class="text-red my-2 text-xs italic">
                  Email is required. (Enter a valid email)
                </p>
              </div>
              <div class="w-full mb-6">
                <label class="block text-gray-400 text-sm font-bold mb-2" for="mailing-address">
                  Message Body <span class="text-red">*</span>
                </label>
                <textarea rows="7" v-model="message_body" @input="checkRequired('message-body', 'mixed')" spellcheck="false" class="shadow block appearance-none rounded w-full py-2 px-4 text-gray-400 leading-tight focus:outline-none focus:bg-light bg-dark-gray focus:border-teal border-2 transition duration-300 ease-out border-charcoal-gray" id="message-body" />
                <p v-if="message_body_error" class="text-red my-2 text-xs italic">
                  Please fill out this field.
                </p>
              </div>
              <p class="text-sm my-2 text-center text-white" v-if="failureMessage">
                <span class="inline-block bg-red p-2 rounded">
                  {{ failureMessage }}</span>
              </p>
              <div class="flex w-full justify-center">
                <button v-if="!saving" @click="submit" class="inline-flex items-center bg-indigo-500 hover:bg-indigo-400 text-white font-bold py-2 px-6 border-b-4 border-indigo-700 hover:border-indigo-500 transition ease-in-out rounded">
                  <svg class="fill-current w-4 md:w-6 h-4 md:h-6 mr-1 md:mr-2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                    <path d="M23 0l-4.5 16.5-6.097-5.43 5.852-6.175-7.844 5.421-5.411-1.316 18-9zm-11 12.501v5.499l2.193-3.323-2.193-2.176zm-8.698 6.825l-1.439-.507 5.701-5.215 1.436.396-5.698 5.326zm3.262 4.287l-1.323-.565 4.439-4.503 1.32.455-4.436 4.613zm-4.083.387l-1.481-.507 8-7.89 1.437.397-7.956 8z" />
                  </svg>
                  Send Mail
                </button>
                <button v-else type="button" class="inline-flex items-center bg-indigo-400 text-white font-bold py-2 px-6 border-b-4 border-indigo-500 rounded transition ease-in-out duration-150">
                  <svg class="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
                    <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Sending Email
                </button>
              </div>
            </div>
          </div>
          <!-- Google Map -->
          <div class="hidden lg:flex mb-6 w-full lg:w-1/2">
            <div class="w-full block lg:flex lg:pl-10 text-center">
              <div class="w-full h-96 rounded-md overflow-hidden shadow-md">
                <img src="@/assets/images/message.svg" alt="message.svg" class="inline-block w-80 m-2 opacity-20 transform -rotate-6 animate-wiggle" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <footer class="bg-darker-blue w-full mt-10">
      <p class="text-center w-full text-gray-500  pt-5 mb-2"> Made with Tailwind<img src="@/assets/images/tailwind.svg" class="animate-wiggle h-4 mx-1 inline-block -mt-1" alt="tailwind" /> & Vue<img src="@/assets/images/vue.svg" alt="vue" class="animate-wiggle w-5 mx-1 inline-block -mt-1" /></p>
      <p class="text-center w-full text-gray-600  pb-5"> Copyright &#169; Hanif {{new Date().getFullYear()}}. All Rights Reserved. </p>
    </footer>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: "About",
  data() {
    return {
      saving: false,
      name: "",
      name_error: false,
      email: "",
      email_error: false,
      subject: "",
      subject_error: false,
      message_body: "",
      message_body_error: "",
      mailer_api: "https://hidden-caverns-22842.herokuapp.com/api/sendmail",
      form_submitted: false,
      successMessage: null,
      failureMessage: null,
    };
  },
  methods: {
    checkRequired(id, type) {
      var input = document.getElementById(id);
      var error = false;
      if (type == "email") {
        var mailformat =
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        error = mailformat.test(input.value) ? false : true;
      } else {
        error = !this.sanitize(id, type).length ? true : false;
      }
      if (this.form_submitted) {
        this.checkForm();
        error
          ? (input.classList.add("border-red-500"),
            input.classList.remove("border-gray-200"))
          : (input.classList.remove("border-red-500"),
            input.classList.add("border-gray-200"));
      }
    },
    sanitize(id, type) {
      var input = document.getElementById(id);
      if (type == "text") {
        input.value = input.value
          .replace(/[^a-zA-Z ]/g, "")
          .replace(".", "")
          .replace(/[0-9]/g, "");
        if (id == "name") {
          this.name = input.value;
        }
      } else if (type == "mixed") {
        if (id == "subject") {
          this.subject = input.value.trimStart();
        }
      }
      return input.value;
    },

    async submit() {
      this.form_submitted = true;
      if (this.checkForm()) {
        await this.sendMail();
      }
    },
    checkForm() {
      // Check if name error
      this.name_error = !this.name.trim();
      if (this.name_error) {
        document.getElementById("name").classList.add("border-red");
        document
          .getElementById("name")
          .classList.remove("border-charcoal-gray");
      }
      if (!this.name_error) {
        document.getElementById("name").classList.remove("border-red");
        document.getElementById("name").classList.add("border-charcoal-gray");
      }
      // Check if subject error
      this.subject_error = !this.subject.trim();
      if (this.subject_error) {
        document.getElementById("subject").classList.add("border-red");
        document
          .getElementById("subject")
          .classList.remove("border-charcoal-gray");
      }
      if (!this.subject_error) {
        document.getElementById("subject").classList.remove("border-red");
        document
          .getElementById("subject")
          .classList.add("border-charcoal-gray");
      }
      //Check if Email Error
      var mailformat =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      this.email_error =
        !this.email.trim() || !mailformat.test(this.email) ? true : false;
      if (this.email_error) {
        document.getElementById("email").classList.add("border-red");
        document
          .getElementById("email")
          .classList.remove("border-charcoal-gray");
      }
      if (!this.email_error) {
        document.getElementById("email").classList.remove("border-red");
        document.getElementById("email").classList.add("border-charcoal-gray");
      }
      //Check if Message body error
      this.message_body_error = !this.message_body.trim();
      if (this.message_body_error) {
        document.getElementById("message-body").classList.add("border-red");
        document
          .getElementById("message-body")
          .classList.remove("border-charcoal-gray");
      }
      this.message_body_error = !this.message_body.trim();
      if (!this.message_body_error) {
        document
          .getElementById("message-body")
          .classList.add("border-charcoal-gray");
        document.getElementById("message-body").classList.remove("border-red");
      }

      if (
        !this.name_error &&
        !this.email_error &&
        this.subject &&
        this.message_body
      ) {
        return true;
      }
    },
    async sendMail() {
      this.failureMessage = null;
      this.saving = true;
      await axios
        .post(this.mailer_api, {
          name: this.name,
          email: this.email,
          subject: this.subject,
          message_body: this.message_body,
        })
        .then((response) => {
          if (response.data.successMessage) {
            this.clearForm();
            this.successMessage = response.data.successMessage;
          } else {
            this.failureMessage = response.data.failureMessage;
          }
          this.saving = false;
        })
        .catch(() => {
          this.saving = false;
          this.failureMessage = "Something went wrong!";
        });
    },

    clearForm() {
      this.name = "";
      this.email = "";
      this.subject = "";
      this.message_body = "";
    },
  },
};
</script>
<style scoped>
img.contact-icon {
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.1))
    drop-shadow(0 2px 4px rgba(0, 0, 0, 0.06));
}
</style>