import anime from "animejs";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import $ from "jquery";
import { createApp } from "vue";
import "./index.css";
import Main from "./Main.vue";

createApp(Main).mount("#app");

window.addEventListener("load", function () {
  $("#preloader").fadeOut("slow");
  $("a").on("click", function (event) {
    if (this.hash !== "") {
      event.preventDefault();
      var hash = this.hash;
      $("html, body").animate(
        {
          scrollTop: $(hash).offset().top,
        },
        800
      );
    }
  });

  //Window resize menu position fix

  $(window).resize(function () {
    if (window.innerWidth >= 720) {
      anime({
        targets: ".menu",
        translateX: [0],
        duration: 200,
        easing: "easeInOutElastic(1, 1)",
        delay: 0,
      });
    }
    else {
      anime({
        targets: ".menu",
        translateX: [-300],
        duration: 0,
        delay: 0,
      });
    }

  });

  // Adding Scroll Highlighting 
  let sections = document.querySelectorAll("section");
  let navItems = document.querySelectorAll("a.nav-item");
  window.addEventListener('scroll', () => {
    let current = '';
    sections.forEach(section => {
      const sectionTop = section.offsetTop;
      const sectionHeight = section.clientHeight
      if (this.pageYOffset > (sectionTop - sectionHeight / 3)) {
        current = section.id
      }
    })
    navItems.forEach(item => {
      item.classList.remove('active');
      const item_href = item.getAttribute('href')
      if (item_href === `#${current}`) {
        item.classList.add('active')
      }
    })
  })


  //Click to Hide element functionality
  let HideElements = $(".clickToHide");
  for (let i = 0; i < HideElements.length; i++) {
    HideElements[i].addEventListener(
      "click",
      (e) => {
        clickToHide(e.target);
      },
      false
    );
  }
  function clickToHide(el) {
    anime
      .timeline({ loop: false })
      .add({
        targets: el.parentNode,
        opacity: [1, 0],
        scale: [0.9, 0],
        duration: 800,
        easing: "easeInOutQuad",
        delay: 0,
        update: function (anim) {
          el.style.filter = "blur(" + (20 * anim.progress) / 100 + "px)";
        },
      })
      .add({
        targets: el.parentNode,
        opacity: [0, 1],
        scale: [2, 0.9],
        duration: 800,
        easing: "easeInOutElastic(1, .6)",
        delay: 2000,
        update: function () {
          el.style.filter = "blur(0px)";
        },
      });
  }
  anime.timeline({ loop: false }).add({
    opacity: [0, 1],
    targets: ".headings .animate-begin",
    translateX: [-400, 0],
    duration: 1000,
    easing: "easeInOutElastic(1, 1)",
    delay: 200,
  });
  anime.timeline({ loop: false }).add({
    targets: ".headings .animate-first",
    opacity: [0, 1],
    scale: [3, 1],
    duration: 2000,
    easing: "easeInOutElastic(1, .8)",
    delay: 200,
  });

  anime
    .timeline({ loop: false })
    .add({
      targets: ".animate-second",
      opacity: [0, 1],
      scale: [0.2, 1],
      duration: 800,
      easing: "easeInExpo",
      delay: 800,
    })
    .add({
      targets: ".animate-third",
      opacity: [0, 1],
      easing: "easeInOutQuad",
      duration: 1000,
      delay: 200,
    })
    .add({
      targets: ".animate-fourth",
      opacity: [0, 1],
      scale: [0, 1],
      easing: "easeInOutQuad",
      duration: 300,
      delay: (el, i) => 200 * (i + 1),
    });
  var targetElem = document.querySelector(".animate-fifth");
  anime
    .timeline({ loop: false })
    .add({
      targets: ".animate-fifth",
      opacity: [0, 1],
      scale: [0, 0.6],
      duration: 1000,
      easing: "easeInOutElastic(1, .8)",
      delay: (el, i) => 200 * (i + 1),
      update: function (anim) {
        targetElem.style.filter = "blur(" + (20 * anim.progress) / 100 + "px)";
      },
    })
    .add({
      targets: ".animate-fifth",
      duration: 4000,
      scale: [0.6, 0.9],
      delay: (el, i) => 200 * (i + 1),
      easing: "easeInOutElastic(1, .8)",
      update: function (anim) {
        if (anim.progress < 20) {
          targetElem.style.filter =
            "blur(" + Math.round(20 - anim.progress) + "px)";
        } else {
          targetElem.style.filter = "blur(" + 0 + "px)";
        }
      },
    })
    .add({
      targets: ".animate-sixth",
      opacity: [0, 1],
      scale: [0, 1],
      easing: "easeInOutQuad",
      duration: 1000,
      delay: (el, i) => 200 * (i + 1),
    });

  $(".menu-drawer").on("click", () => {
    anime({
      targets: ".menu",
      translateX: [-300, 0],
      duration: 200,
      easing: "easeInOutElastic(1, 1)",
      delay: 0,
    });
    $(".menu").addClass("active");
    $(".menu-drawer").addClass("hidden");
    $(".menu-close").removeClass("hidden");
  });
  $(".menu-close").on("click", () => {
    anime({
      targets: ".menu",
      translateX: [0, -300],
      duration: 200,
      easing: "easeInOutElastic(1, 1)",
      delay: 0,
    });
    $(".menu").removeClass("active");
    $(".menu-drawer").removeClass("hidden");
    $(".menu-close").addClass("hidden");
  });

  // Working with GSAP

  gsap.registerPlugin(ScrollTrigger);
  gsap.from(".service", {
    scrollTrigger: {
      trigger: ".service",
      toggleActions: "play pause reverse pause",
      start: "-120px 90%",
      end: "top bottom",
      scrub: 3,
    },
    scale: 0.5,
    opacity: 0,
    duration: 0.75,
  });
  gsap.from(".about-photo, .about-me-details", {
    scrollTrigger: {
      trigger: ".about-photo, .about-me-details",
      toggleActions: "play pause reverse pause",
      start: "-120px 90%",
      end: "top bottom",
      scrub: 3,
    },
    scale: 0.5,
    opacity: 0,
    duration: 1,
  });
  gsap.from(".socials", {
    scrollTrigger: {
      trigger: ".socials",
      toggleActions: "play pause resume pause",
    },
    scale: 0.5,
    opacity: 0,
    duration: 1,
  });
  gsap.from(".education", {
    scrollTrigger: {
      trigger: ".education",
      toggleActions: "play pause resume pause",
    },
    scale: 0.5,
    opacity: 0,
    duration: 0.75,
  });
  gsap.from(".skills", {
    scrollTrigger: {
      trigger: ".skills",
      toggleActions: "play pause resume pause",
    },
    scale: 0.5,
    opacity: 0,
    duration: 1,
  });
  gsap.from(".contact", {
    scrollTrigger: {
      trigger: ".contact",
      toggleActions: "play pause resume pause",
    },
    y: 200,
    opacity: 0,
    duration: 1,
  });
});
